    gap: 25px;
  }
}

.post-it__form-input-container {
  flex: 1;
}

.post-it-form__label {
  font-size: 18px;
}

.post-it-form__input {
  border: 2px solid #000;
  border-radius: 4px;
  width: 100%;
  height: 55px;
  margin-top: 5px;
  padding: 10px;
}

.post-it-form__button {
  cursor: pointer;
